const H2 = (props: any) => {
  const { children, sub }: { children: React.ReactNode; sub: string } = props;

  return (
    <Transition>
      <Box mb={-2}>
        <Typography variant='caption'>{sub}</Typography>
      </Box>
      <Typography variant='h2' color='primary'>
        {children}
      </Typography>
    </Transition>
  );
};

const Transition = ({
  children,
  delay = 0
}: {
  children: React.ReactNode;
  delay?: number;
}) => {
  const { ref, inView } = useInView({
    // 画面の上下中央を inview の基準線にする
    rootMargin: '-25% 0px',
    threshold: 0,
    triggerOnce: true
  });

  return (
    <Box
      ref={ref}
      sx={{
        width: '100%',
        height: '100%'
      }}
    >
      <Box
        sx={{
          width: '100%',
          height: '100%',
          transition: [
            `opacity 1000ms ease-out ${delay}ms`,
            `transform 1000ms ease-out ${delay}ms`
          ].join(','),
          transform: `translateX(${inView ? 0 : 100}px)`,
          opacity: inView ? 1 : 0
        }}
      >
        {children}
      </Box>
    </Box>
  );
};

